/* 
  about
  ----- 
  */

#about {
  padding: 5rem 0;
  min-height: 80vh;
  display: flex;
  align-items: center;
  justify-content: center;
}

.about-heading {
  font-weight: bold;
  position: relative;
}

.about-heading:after {
  transform: translateX(-50%);
  content: "";
  background: #1890ff;
  position: absolute;
  bottom: -14px;
  left: 50%;
  height: 3px;
  width: 50px;
}

.about-content {
  padding: 1rem 0;
  margin: 0;
}

.aboutBlock .contentHolder {
  text-align: center;
  margin: 0 0 50px;
}

.aboutBlock .icon {
  font-size: 40px;
  color: #1890ff;
  margin: 0 0 10px;
}

.aboutBlock .content {
  text-align: center;
}

